@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300&family=Roboto+Mono:wght@600&display=swap');
.about_background{
    width: 100%;
    padding: 25px 0px 25px 0px;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.9) 0%,rgba(255, 255, 255, 0.8) 100%), url("../../img/elysium.png");
}
.about_title{
    padding: 25px 0px 15px 0px;
    font-size: 1cm;
    text-align: center;
    font-family: 'Roboto Mono', monospace;
}
.about_content_frame{
    width: 80%;
    margin-left: 10%;
}
.about_content_text{
    padding: 10px 0px 10px 0px;
    font-size: 0.6cm;
    text-align: center;
    font-family: 'Quicksand', sans-serif;
}
.about_fade_in{
    animation: fade_in 1.5s forwards;
}
@keyframes fade_in {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}