@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300&family=Roboto+Mono:wght@600&display=swap');
.sidebar_frame{
    width: 50px;
    position: fixed;
    z-index: 100;
    margin-left: 2%;
    margin-top: 2%;
}
.sidebar_circle{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #ffffff;
    opacity: 0.6;
    position: relative;
    box-shadow: 1px 1px 1px gray;
}
.sidebar_line{
    width: 2.5px;
    height: 50px;
    background-color: #818181;
    opacity: 0.8;
    position: relative;
    margin: auto;
}
.sidebar_text{
    padding: 15px 0px 0px 0px;
    font-size: 15px;
    font-family: 'Quicksand', sans-serif;
    text-align: center;
    cursor: pointer;
    color: black;
}
.sidebar_current{
    background-color: rgb(255, 244, 94);
}